import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ExchangeVoucherComponent } from './exchange-voucher.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgZorroAntModule } from '../ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular'; 
import { ComponentsModule } from './components/components.module';
import { ConfigPrintComponent } from './config-print/config-print.component';

const routes: Routes = [
  {
    path: '',
    component: ExchangeVoucherComponent,
  }
]


@NgModule({
  declarations: [
    ExchangeVoucherComponent, ConfigPrintComponent
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    RouterModule.forChild(routes),
    ComponentsModule
  ]
})
export class ExchangeVoucherModule { }
