import { Router } from '@angular/router';
import { ChangeDetectorRef, Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { VhAlgorithm, VhAuth, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhBillType41Add } from 'src/app/cafe/components/bill-type/bill-type-41/bill-type41-interface';
import { ExchangeVoucherK57Col3Component } from 'src/app/cafe/components/print/exchange-voucher/exchange-voucher-k57-col3/exchange-voucher-k57-col3.component';
import { ExchangeVoucherK80Col3Component } from 'src/app/cafe/components/print/exchange-voucher/exchange-voucher-k80-col3/exchange-voucher-k80-col3.component';
import { ExchangeVoucherK80Col4Component } from 'src/app/cafe/components/print/exchange-voucher/exchange-voucher-k80-col4/exchange-voucher-k80-col4.component';
import { ExchangeVoucherA5Component } from 'src/app/cafe/components/print/exchange-voucher/exchange-voucher-a5/exchange-voucher-a5.component';
import { ExchangeVoucherA4Component } from 'src/app/cafe/components/print/exchange-voucher/exchange-voucher-a4/exchange-voucher-a4.component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-exchange-voucher',
  templateUrl: './exchange-voucher.component.html',
  styleUrls: ['./exchange-voucher.component.scss']
})
export class ExchangeVoucherComponent implements OnInit {
  @ViewChild('keySearch') keySearch: ElementRef;
  /**X l branches */
  public branchList: Array<any> = []                      // Danh sch chi nhnh
  public branchDefault: any = {};                         // Chi nhnh mc nh
  public user: any = this.vhAuth.getUser()                // thng tin ti khon hin ti
  public dateCurrent: Date = new Date;                   // Ngy hin ti
  public listVoucherAccepct: any[] = []                    // danh sch voucher  iu kin
  public codeVoucher: string;                             // M voucher va chn
  public listShow: any[] = []
  public heightScroll: string;                            // Chiiu di sroll
  public modalAccountInfo: boolean = false;               // show/hide moal thng tin ti khon
  public showConfig = false;                              // bin show modal cu hnh
  public showConfigPrint = false;                         // bin show modal cu hnh may in
  public showConfigSales = false;                         // bin show modal cu hnh trang bn hng
  public checkPrinter: boolean = true;
  isVisible: boolean = false;
  public invoice: VhBillType41Add = {
    bill_type: 41,
    date: new Date(),
    id_branch: this.vhQueryCafe.getDefaultBranch()._id,
    id_customer: 'id_retail',
    id_employee: this.vhAuth.getUser()._id,
    note: '',
    tax: 0
  };
  customer: any;
  public employee: any = {};
  public listVoucher: any;                                // Danh sch voucher
  public billDetails: any[];
  templates: { name: string; url: string; giai_thich: string; }[] = [];
  store_main_sector = '';
  country_code = 'VN';
  language = 'vi';

  constructor(
    public vhQueryCafe: VhQueryCafe,
    public vhAuth: VhAuth,
    private vhQuery: VhQuery,
    public vhAlgorithm: VhAlgorithm,
    public vhComponent: VhComponent,
    public languageService: LanguageService,
    public router: Router,
    public cdRef: ChangeDetectorRef,
    private nzModalService: NzModalService,
    private http: HttpClient,
  ) { }

  ngOnInit(): void {
    this.customer = this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer);
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    this.vhQueryCafe.getVouchers().then((arr) => {
      this.listVoucher = arr
    }, error => {
      console.log('error', error);
    })

    this.store_main_sector = this.vhAuth.getStore().main_sector; // Ly main_sector ca ca hng
    this.http.get(`assets/documents/configuration/template/${this.country_code}/${this.store_main_sector}.json`).subscribe((templates) => {
      this.templates = templates[this.language];
    }); // Ly giao din theo main_sector ca ca hng
  }

  ngAfterViewChecked() {
    if (document.querySelector("#exchange-voucher") &&
      document.querySelector(".ant-table-thead") &&
      document.querySelector(".button") &&
      document.querySelector(".header-content")) {
      this.heightScroll =
        document.querySelector("#exchange-voucher").clientHeight -
        document.querySelector(".ant-table-thead").clientHeight -
        document.querySelector(".button").clientHeight -
        document.querySelector(".header-content").clientHeight - 80 + "px";
    }

    this.cdRef.detectChanges();
  }
  // X l dng voucher
  useVoucher(code_voucher: string) {
    this.tempDetail = []
    if (!this.listVoucherAccepct.find(item => item.code == code_voucher)) {
      this.vhComponent.showLoading('')
        .then(() => {
          this.vhQueryCafe.getVoucher_Code_Name_Release_byCode(code_voucher).then((voucherCode: any) => {
            console.log('getVoucher_Code_Name_Release_byCode', voucherCode);
            if (voucherCode) {
              switch (voucherCode.status) {
                case 1: {
                  this.vhComponent.hideLoading(0);
                  this.vhComponent.alertMessageDesktop('error', this.languageService.translate('Voucher cha bn !'));
                  break;
                }
                case 4: {
                  this.vhComponent.hideLoading(0);
                  this.vhComponent.alertMessageDesktop('error', this.languageService.translate('Voucher  c s dng !'));
                  break;
                }
                default: {
                  if (this.dateCurrent > new Date(voucherCode.date_expire)) {
                    this.vhComponent.hideLoading(0)
                    this.vhComponent.alertMessageDesktop('error', this.languageService.translate('Voucher  ht hn !'));
                    break;
                  } else {
                    this.vhComponent.hideLoading(0);
                    this.listVoucherAccepct = this.listVoucherAccepct.concat(voucherCode);
                    this.listShow = this.listVoucherAccepct;
                    // check xem products c sp c lots hay ko
                    if (voucherCode['products'].filter(pro => pro.ptype == 1 && pro.lots && pro.lots.length).length) {
                      console.log('sp c lots');
                      this.show_modal_select_lots_combo = true;
                      this.tempDetail = this.tempDetail.concat(voucherCode['products'].filter(pro => pro.lots && pro.lots.length))
                    }

                    // check trong products c combo c lots ko
                    for (let i of voucherCode['products'].filter(pro => pro.ptype == 5)) {
                      if (i.combos.filter(e => e.lots && e.lots.length).length) this.tempDetail.push(i)
                    }
                    console.log(this.tempDetail);

                    this.vhComponent.alertMessageDesktop("success", this.languageService.translate("p dng thnh cng voucher !"))
                    break;
                  }
                }
              }
            } else {
              this.vhComponent.hideLoading(0)
              this.vhComponent.alertMessageDesktop('error', this.languageService.translate('Voucher khng hp l !'));
            }
          }, err => {
            this.vhComponent.alertMessageDesktop('error', this.languageService.translate(err))
          })
        })

    } else {
      this.vhComponent.alertMessageDesktop('error', this.languageService.translate('ang p dng voucher ny!'))
    }
  }


  deleteGoods(position) {
    if (this.listShow.length == 1) {
      this.listShow = []
    } else {
      this.listShow.splice(position, 1);
    }
    this.listVoucherAccepct = this.listShow
  }

  gotoHome() {
    this.router.navigate(['/cafe/dashboard/overviews'], { state: { isSync: true } });
  }



  logout() {
    this.vhAuth.signOut().then(() => {
      this.vhQueryCafe.closeSyncCollections();
      this.vhQueryCafe.clearDefaultBranch() //xa branchDefault trc khi thot app
      this.router.navigate(["/login"]);
    }, error => {
      console.error('error', error);
    })
  }

  //  x l i voucher v m thng bo
  exchangeVoucher() {
    console.log(this.listVoucherAccepct);

    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      let promise = new Array();
      for (let i = 0; i < this.listVoucherAccepct.length; i++) {
        // kim tra voucher  dng hay cha
        promise[i] = this.vhQueryCafe.getVoucher_Code_Name_Release_byCode(this.listVoucherAccepct[i].code)
      }
      this.vhQuery.asyncPromiseAll(promise).then((array: any[]) => {
        // x l cc voucher no hp l  to bill_detail
        let voucher_valid = array.filter(item => item.status == 2 || item.status == 3);
        let bill_detail = [];
        for (let i of voucher_valid) {
          bill_detail.push(this.listVoucherAccepct.find(e => e._id == i._id))
        }
        if (bill_detail.length)
          this.billDetails = bill_detail.map(m => {
            // bill details
            m = {
              id_voucher: m.id_voucher,
              id_voucher_code: m._id,
              vtype: 1,
              quantity: 1,
              products: m.products.map(map => {
                let product = {
                  id_product: map.id_product,
                  price: 0,
                  ptype: map.ptype,
                  quantity: map.quantity,
                  ratio: map.ratio,
                }
                if (map.id_lotproduct) product['id_lotproduct'] = map.id_lotproduct;
                if (map.id_subproduct) product['id_subproduct'] = map.id_subproduct
                if (map.ptype == 5) {
                  product['combos'] = map.combos.map(combo => {
                    let combos = {
                      id_product: combo.id_product,
                      price: combo.price,
                      quantity: combo.quantity,
                      ptype: combo.type,
                      ratio: combo.ratio ? combo.ratio : 1,
                    }
                    if (combo.id_lotproduct) combos['id_lotproduct'] = combo.id_lotproduct
                    if (combo.id_subproduct) combos['id_subproduct'] = combo.id_subproduct
                    return combos;
                  })
                  console.log(product['combos']);
                }
                return product
              })
            }
            return m;
          });
        // bill
        let dataBill = this.invoice
        console.log('billDetails', this.billDetails);
        // this.vhComponent.hideLoading(0)
        if (this.billDetails.length == this.listVoucherAccepct.length) {
          this.vhQueryCafe.createBill_Billdetail(dataBill, this.billDetails).then((bill: any) => {
            console.log('createBill_Billdetail', bill);

            if (bill) {
              this.invoice.id_customer = 'id_retail'
              this.handlePrint(dataBill, this.listShow);
              this.vhComponent.hideLoading(0)
              this.vhComponent.alertMessageDesktop("success", "i voucher thnh cng !");
            }
          }, (error: any) => {
            console.log('error ', error);
            this.vhComponent.hideLoading(0)
            this.vhComponent.alertMessageDesktop("error", "i voucher tht bi !");
          })
        } else {
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("C voucher khng hp l! Vui lng kim tra li."));
          this.vhComponent.hideLoading(0)
        }
      }, error => {
        console.log(error);
        this.vhComponent.hideLoading(0)
      })
    })
  }



  // hy b i voucher
  cancelExchangeVoucher() {
    this.listVoucherAccepct = []
    this.listShow = []
  }


  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  private checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_exchange_voucher')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects(false)
    })

  }

  handlePrint(bill: VhBillType41Add, bill_details) {
    this.checkPrint().then((printer: any) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: bill, invoice_detail: bill_details }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.cancelExchangeVoucher())
        })
    }, (err) => {
      this.nzModalService.confirm({
        nzTitle: '<i>' + this.languageService.translate("Function") + '</i>',
        nzContent: '<b>' + this.languageService.translate("Please open the function in") + ' ' + '<i>' + '"' + this.languageService.translate("Sales page settings") + '"' + '</i>' + ' ' + ' -> ' + '<i>' + '"' + this.languageService.translate('Print page') + '"' + '</i>' + '</b>',
        nzIconType: "info-circle",
        nzCancelText: null,
        nzOnOk: () => { this.cancelExchangeVoucher() }
      })
    })
  }



  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExchangeVoucherK57Col3Component
      case 'print_size_k80_3c': return ExchangeVoucherK80Col3Component
      case 'print_size_k80_4c': return ExchangeVoucherK80Col4Component
      case 'print_size_a5': return ExchangeVoucherA5Component
      case 'print_size_a4': return ExchangeVoucherA4Component
      default: return ExchangeVoucherA4Component
    }
  }


  showConfirm(): void {
    if (this.listVoucherAccepct.length > 0) {
      this.nzModalService.confirm({
        nzTitle: 'Xc nhn i voucher',
        nzOkText: 'Xc nhn v In',
        nzOkType: 'primary',
        nzOkDanger: true,
        nzOnOk: () => this.exchangeVoucher(),
        nzCancelText: 'Hy',
        nzOnCancel: () => this.cancelExchangeVoucher()
      });
    }
  }


  /**add customer */
  // ----set customer-----
  radioValue: 1 | 2 = 1;
  showDrawerAddCusomer: boolean = false;
  showDrawerAddRetail: boolean = false
  openDrawerAddCustomerOrRetail() {
    if (this.radioValue == 1) this.showDrawerAddRetail = true
    else this.showDrawerAddCusomer = true
  }

  closeAddCustomer(result) {
    if (this.radioValue == 1) {
      if (result) {
        this.invoice.retail_name = (result.retail_name)
        this.invoice.retail_phone = (result.retail_phone)
        this.invoice.retail_address = (result.retail_address)
      }
      this.showDrawerAddRetail = false
    }
    else {
      this.showDrawerAddCusomer = false;
      if (result)  this.invoice.id_customer = result
    }
  }

  closeAndReceiveCustomer(event) {
    if (event) {
      this.invoice.id_customer = (event);
    }
    this.customer = this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer);

  }
  //--------------visible drawer info customer-----------
  visibleInfoCustomer: boolean = false;
  openInfoCustomer() {
    if (this.invoice.id_customer != 'id_retail') {
      this.customer = this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer);
      this.visibleInfoCustomer = true;
    }
  }

  closeInfoCustomer(event) {
    this.visibleInfoCustomer = false;
  }
  changeCustomerType(event) {
    if (this.radioValue == 1) {
      this.invoice.id_customer = ("id_retail");
    } if (event == 2 && this.invoice.retail_name) {
      this.nzModalService.confirm({
        nzTitle: `${this.languageService.translate('Retail customer information will be lost')}?`,
        nzCancelText: this.languageService.translate("Cancel"),
        nzOkText: "Ok",
        nzOnOk: () => { this.clearRetailCustomer() },
        nzOnCancel: () => { this.radioValue = 1 },
      }
      )
    }
  }

  clearRetailCustomer() {
    this.invoice.retail_address = ('')
    this.invoice.retail_name = ('')
    this.invoice.retail_phone = ('')
  }

  barcode: string = ''
  barcode_array = [];
  @HostListener("window: keypress", ["$event"])
  /**
   * S kin nhn barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === "/change-voucher") {
      if (_event.key == "Enter") {
        this.barcode_array.forEach((i, index) => {
          if (i == "Shift") {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });
        this.barcode = this.barcode_array.join("");
        setTimeout(() => {
          this.barcode_array = []
        }, 200);
      } else {
        this.barcode_array.push(_event.key);

      }
    }
  }
  show_modal_choose_interface = false
  /**
  * thit lp giao din trang bn hng
  * @param type sale-pos | sale-desktop
  */
  public updateInterface() {
    if (this.vhAuth.checkMyPermission('selling_display_page')) {
      if (!this.default_selling_page)
        this.show_modal_choose_interface = true
      else this.vhAuth.updateEmployee(this.vhAuth.getUser()._id, { default_display_page: 'selling_display_page' })
        .then((bool) => {
          this.router.navigate(['/cafe/'+this.default_selling_page], { state: { isSync: true } });
        })

    }
    else this.vhComponent.alertMessageDesktop("error", this.languageService.translate("You do not have this rights"))
  }
  default_selling_page = this.vhAuth.getLocalMyPermissionName('default_selling_page')
  /**
    * thit lp giao din trang bn hng  sale-pos | sale-desktop
    *
    */
  updateSalesInterface() {
    this.vhAuth.updateEmployee(this.user._id, { default_selling_page: this.default_selling_page, default_display_page: 'selling_display_page' })
      .then((bool) => {
        this.router.navigate(['/cafe/'+this.default_selling_page], { state: { isSync: true } })
      })
  }

  gotoPrepaidCard() {
    this.router.navigateByUrl('/cafe/prepaid-card', { state: { url: this.router.url } })
  }

  /**ng modal v get li thng tin user */
  closeAccountInfo(event) {
    this.user = this.vhAuth.getlocalEmployee(this.vhAuth.getUser()._id)
    this.modalAccountInfo = false;
  }
  gotoSalesVoucher() {
    this.router.navigateByUrl('/cafe/sales-voucher')
  }

  gotoSalesPrepaidCard() {
    this.router.navigateByUrl('/cafe/sales-payment-card')
  }
  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  tempDetail: any = {}
  show_modal_select_lots_combo = false;
  handleChooseLotCombo() {
    let check = false;
    for (let i of this.tempDetail) {
      if (i.ptype == 1 && !i.id_lotproduct) check = true;
      if (i.ptype == 5 && i.combos.filter(item => item.lots && !item.id_lotproduct).length) check = true;
    }
    if (check)
      this.vhComponent.alertMessageDesktop("warning", this.languageService.translate("Please select the lot for the product!"))
    else {
      this.show_modal_select_lots_combo = false;
      // this.closeSearchProductList(this.tempDetail)
    }
  }

  gotoSalesOrder() {
    this.router.navigateByUrl('/cafe/bill-web-app/sales-order')
  }
  gotoSalesOrderVoucher() {
    this.router.navigateByUrl('/cafe/bill-web-app/sales-order-voucher')
  }
  gotoExchangeOrderVoucher() {
    this.router.navigateByUrl('/cafe/bill-web-app/exchange-order-voucher')
  }
  gotoSalesOrderPrepaidCard() {
    this.router.navigateByUrl('/cafe/bill-web-app/sales-order-payment-card')
  }
  gotoExchangeOrderCard() {
    this.router.navigateByUrl('/cafe/bill-web-app/exchange-order-payment-card')
  }
}
